*,*::before,*::after{-webkit-box-sizing:border-box;box-sizing:border-box;margin:0;padding:0;border:1px dashed grey}body{min-height:100vh}main{min-height:100vh;display:-ms-grid;display:grid;place-content:center}.article{display:-webkit-box;display:-ms-flexbox;display:flex}.article__body{display:-webkit-box;display:-ms-flexbox;display:flex}.article{font-family:"Manrope",sans-serif}.article__header{font-size:20px;font-weight:700;line-height:28px;letter-spacing:0.25px;color:#48556a}.article__paragraph{font-size:13px;font-weight:500;line-height:20px;letter-spacing:0.12px;color:#6d7f97}.article__publisher-name{font-size:13px;font-weight:700;line-height:20px;letter-spacing:0.12px;color:#48556a}.article__date{font-size:13px;font-weight:500;line-height:20px;letter-spacing:0.12px;color:#9eafc2}@media screen and (max-width: 800px){.article{font-family:"Manrope",sans-serif}.article__header{font-size:16px;font-weight:700;line-height:24px;letter-spacing:0.2px;color:#48556a}}
